/*@media only screen and (min-width: 1366px) {*/

    /* -- HEADER -- */
    #headerBackground {
        width: 100%;
        z-index: 15;
        background-color: white;

        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }
    #header{
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        z-index: 8;
    }
    #headerLogo{
        height: 42px;
    }
    #headerMediaPlugins{
        height: 15px;
        width: 170px;
    }
    #headerMediaPlugins img {
        height: 100%;
        margin: 0 2px;
    }
    #headerMediaPlugins img:hover {
        cursor: pointer;
    }
    #headerPanel{
        width: 1030px;
        height: 60px;
    }

    /* -- SEARCHBAR -- */
    .searchBar {
        float: right;
    }
    .searchForm {
        float: right;
    }
    .fa{
        height: 30px;
    }
    .searchForm input{
        height: 30px;
        width: 120px;

        border: solid 1px rgba(0, 0, 0, 0.3);
        border-radius: 30px;
        padding: 0 40px 0 15px;

        font-weight: 400;
        color: rgba(0, 0, 0, 0.6); 
        background-color: white;
        
    }
    .searchForm button{
        padding: auto 10px;
        margin-left: -45px;

        border:none;
        cursor: pointer;
        background-color: transparent;
    }
    .searchForm img {
        height: 16px;
    }

    /* -- NAVBAR -- */
    .navbar {
        width: 100%;
        height: 38px;

        overflow: hidden;
        background-color: #e10600;
        box-shadow: 0 0px 8px rgba(0, 0, 0, 0.5);
    }
    .navbar a{
        padding: 7px 19px;

        text-decoration: none;
        text-transform: uppercase;
        font-family: Source Sans Pro;
        font-weight: 700;
        font-size: 14px;
        color: white;

        border-bottom: 5px solid transparent;
        border-top: 5px solid transparent;
    }
    .navbar a:hover{
        border-bottom-color: white;
    }
    .navbar a.selected {
        border-bottom-color: white;
    }

    
    
    



    .reGrayTitle {
        margin: 0;
        padding: 7px;
        padding-left: 12px;

        
        font-weight: 600;
        font-size: 16px;
        font-family: Source sans pro;
        text-transform: uppercase;
        
        background-color: #eceff2;
        border-left: 5px solid #e10600;
    }
    
    /* UTILS */
    .flex-space-between {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .flex-center {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .titleFont {
        font-family: Source sans pro;
        font-weight: bold;
    }
    .contentFont {
        font-family: Open sans;
        font-weight: 400;
    }
    .subTitleFontColor {
        color: rgba(0, 0, 0, 0.65);
    }
    .fs-13 {
        font-size: 13px;
    }
    .fs-14 {
        font-size: 14px;
    }
    .fs-16 {
        font-size: 16px;
    }
    .fs-18 {
        font-size: 18px;
    }
    .fw-normal {
        font-weight: 400;
    }
    .fw-600 {
        font-weight: 600;
    }
    .blackBotGradient {
        background: linear-gradient(0deg, 
        rgba(1,1,1,0.95) 0%, 
        rgba(1,1,1,0.75) 20%, 
        rgba(0,0,0,0.50) 75%,
        rgba(0,0,0,0.00) 100%);
    }
    .zoomImageContainer img{
        transition: transform .3s ease;
    }
    .zoomImageContainer:hover img {
        transform: scale(1.025);
    }
    .basicButton {
        margin: auto;
        height: 100%;
        width: 130px;
        border: 1px solid black;
        border-radius: 0px;
        font-family: Source sans pro;
        text-transform: uppercase;
        font-weight: 600;
        font-size: 14px;
        color: black;
        background-color: white;
    }
    .linkAsButton {
        margin-top: 10px;
        margin-right: 0px;
        margin-left: auto;
        width: fit-content;
    }
    .linkAsButton:hover a{
        text-decoration: underline;
    }
    .linkAsButton a {
        font-size: 14px;
        text-decoration: none;
        color: black;
        font-family: Source sans pro;
        font-weight: 600;
    }
    .linkAsButton img {
        margin-bottom: -1px;
        height: 11px;
        width: 11px;
        padding-left: 3px;
    }







    
    
    

    

    /* -- MAINZONE AREA -- */
    .titleArea{
        display: grid;
        grid-template-columns: auto 246px 20px 246px 20px 240px 20px 240px auto; 
        grid-template-rows: 40px 150px 20px 150px;
    }
    #art-00 {
        grid-column: 2 / span 3;
        grid-row: 2 / span 3;
    }
    #art-01 {
        grid-column: 6 / span 1;
        grid-row: 2 / span 1;
    }
    #art-02 {
        grid-column: 6 / span 1;
        grid-row: 4 / span 1;
    }
    #art-03 {
        grid-column: 8 / span 1;
        grid-row: 2 / span 1;
    }
    #art-04 {
        grid-column: 8 / span 1;
        grid-row: 4 / span 1;
    }

    .articleLink {
        text-decoration: none;
    }
    /* -- MAIN ARTICLE -- */
    .mainArticlePreview{
        position: relative;
        overflow: hidden;
        height: 320px;
        z-index: 0;
    }
    .mainArticlePreview img{
        height: 100%;
        vertical-align: middle;
    }
    .mainArticleTitle{
        height: 70px;

        position: relative;
        bottom: 0px;
        margin-top: -70px;
    }
    .mainArticleTitle h3 {
        margin: 0;
        padding: 12px;

        text-align: start;
        color: white;
        text-shadow: black 2px 2px 2px;
    }
    
    .sideArticlePreview {
        height: 100%;
        position: relative;
        overflow: hidden;
    }
    .sideArticlePreview img{
        height: 100%;
        vertical-align: middle;
    }
    .sideArticlePreview:hover img{
        transform: scale(1.05);
    }
    
    .sideArticleTitle {
        height: 70px;
        bottom: 0px;
        margin: 0;
        width: 100%;
        position: absolute;
    }
    .sideArticleTitle h3{
        padding: 10px 10px;
        margin: 0;
        overflow: auto;
        color: white;
        text-shadow: black 1px 1px 2px;
    }
    
    

    .articlesBlock {
        display: grid;
        grid-template-columns: auto 196px 20px 196px 20px 196px 38px 365px auto;
        grid-template-rows: 30px 50px 15px 220px 20px 220px 20px 40px;
    }
    #art-05 {
        grid-column: 2 / span 1;
        grid-row: 4 / span 1;
    }
    #art-06 {
        grid-column: 4 / span 1;
        grid-row: 4 / span 1;
    }
    #art-07 {
        grid-column: 6 / span 1;
        grid-row: 4 / span 1;
    }
    #art-08 {
        grid-column: 2 / span 1;
        grid-row: 6 / span 1;
    }
    #art-09 {
        grid-column: 4 / span 1;
        grid-row: 6 / span 1;
    }
    #art-10 {
        grid-column: 6 / span 1;
        grid-row: 6 / span 1;
    }

    .articlesBlockTitle {
        grid-column: 2 / span 1;
        grid-row: 2 / span 2;
    }
    .articlesBlockTitle h2{
        margin: 0px;
        padding: 5px;
        font-size: 26px;
    }

    .side {
        grid-column: 8 / span 1;
        grid-row: 4 / span 4;
    }
    #quickNewsTitle {
        display: block;
    }
    #quickNewsBlock {
        margin-top: 5px;
    }

    /* -- ARTICLE PREVIEW -- */
    .articlePreview img {
        width: 100%;
    }
    .articleTitle {
        color: black;
    }
    .articleTitle h3 {
        margin: 0;
        height: 60px;
        padding-top: 4px;
    }
    .articleTitle .title:hover {
        text-decoration: underline;
    }
    .articleTitle .date {
        margin: 0;
        font-weight: 400;
    }

    /* -- ONELINERS -- */
    .scrollable {
        height: 460px;
        padding: 10px;
        padding-top: 0px;
        margin-bottom: 25px;
        overflow-y: scroll;
    }
    .oneLineNews{
        z-index: 5;
    }
    .oneLineNews .date {
        width: fit-content;
        padding: 0 6px;

        position: relative;
        margin-bottom: -20px;
        margin-left: 15px;

        z-index: 3;
        font-family: Open sans;
        font-size: 11px;
        background-color: white;
    }
    .oneLineNews .message {
        padding: 8px;
        padding-top: 12px;

        z-index: 0;
        border: 1px solid rgba(0, 0, 0, 0.3);
    }
    .oneLineMessageLink {
        text-decoration:  none;
        color: black;
    }
    .oneLineMessageLink:hover{
        text-decoration:  underline;
    }

    /* -- all articles BUTTON -- */
    .allArticlesButton{
        grid-column: 4 / span 1;
        grid-row: 8 / span 1;
    }
    .allArticlesButton:hover {
        border: 1px solid #e10600;
        color: #e10600;
        cursor: pointer;
    }
    
    /* -- CALENDAR basics -- */
    .calendarPreview {
        display: grid;
        grid-template-columns: auto 307px 14px 307px 38px 365px auto;
        grid-template-rows: 10px 55px 10px 35px 10px 192px 0px 30px 10px 35px 250px 10px;
    }
    .calendarPreview .calendarTitle{
        grid-column: 2 / span 1;
        grid-row: 2 / span 1;

        margin: 0px;
        margin-top: 10px;
        padding: 5px;
        
        font-size: 26px;
    }
    .calendarPreviewImage {
        grid-column: 2 / span 1;
        grid-row: 6 / span 1;
        height: 100%;
    }
    .calendarPreview .venueTitle {
        grid-column: 2 / span 3;
        grid-row: 4 / span 1;
    }
    .calendarPreview .venueName {
        margin: 0;
        font-weight: 600;
    }
    .calendarPreview .timesData {
        grid-column: 4 / span 1;
        grid-row: 6 / span 1;
    }
    .calendarPreview .linkAsButton {
        grid-column: 4 / span 1;
        grid-row: 8 / span 1;
        
        margin-right: 5px;
        margin-left: auto;
    }
    
    /* PROGRAM -- */
    .timesDataItem {
        margin: 0px;
        margin-top: 13px;
        margin-right: 10px;
        width: 143px;

        display: flex;
        flex-direction: column;
        align-items: flex-start;
        border-left: 3px solid #d8dcde;

        z-index: 0;

        -webkit-user-select: none;  
        -moz-user-select: none;    
        -ms-user-select: none;      
        user-select: none;    
    }
    .timesDataItem .event {
        height: 20px;
        padding-left: 7px;
        font-weight: 600;
    }
    .timesDataItem .tvStations {
        padding-left: 5px;
        position: absolute;
        z-index: 2;

        background-color: #eceff2;
        display: none;
    }
    .timesDataItem:hover .tvStations {
        color: black;
        display: block;
    }
    .timesDataItem .time {
        z-index: 1;
        color: rgba(0, 0, 0, 0.65);
    }
    .timesData .date {
        margin: 0;
    }
    .timesData .venueDetails {
        padding-left: 7px;
        border-left: 3px solid #e10600;
    }

    .inRow {
        display: relative;
        padding-left: 0px;
        height: 18px;
    }
    .inRow .time {
        position: absolute;
        padding-left: 7px;
    }
    .inRow .tvStations {
        position: absolute;
        color: rgba(0,0,0,0);
    }
    .cols {
        display: flex;
        flex-direction: row;
    }
    .col1 {
        display: flex;
        flex-direction: column;
    }
    .col2 {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-end;
    }

    /* RESULTS */
    .resultsInfoboxTitle {
        grid-column: 2 / span 1;
        grid-row: 10 / span 1;
    }
    .resultsInfobox {
        grid-column: 2 / span 1;
        grid-row: 11 / span 1;

        margin-left: 5px;
        margin-right: 8px;
    }
    .resultsDataItem {
        margin: 0;
        margin-top: 10px;
        padding-left: 10px;
        width: 100%;

        display: flex;
        flex-direction: row;
    }
    .resultsDataItem span{
        margin: 0px;
        padding: 0px 5px;
        display: block;
    }
    .resultsInfobox .name {
        width: 190px;
        font-weight: 600;
    }
    .resultsInfobox .time {
        width: 110px;
    }
    .resultsInfobox .position {
        width: 12px;
        font-weight: 600;
        flex-shrink: 0;
    }

    .champResultsInfoboxTitle {
        grid-column: 4 / span 1;
        grid-row: 10 / span 1;
    }
    .champResultsInfobox {
        grid-column: 4 / span 1;
        grid-row: 11 / span 1;
    }
    
    .driverData {
        margin: 0px;
        margin-right: 10px;
        width: 160px;
        height: 35px;
        border-left: 3px solid #d8dcde;
    }
    .driverData .team {
        color: rgba(0, 0, 0, 0.65);
    }
    .podium-line {
        border-color: #e10600;
    }/*
}*/